.ContactInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;

    min-height: 130px;
    margin-top: 40px;
    margin-bottom: 40px;
	text-align: left;
	font-size: 21px;
    line-break: anywhere;
}

.contact-title {
    width: 100px;
}

.phone {
    margin-bottom: 20px;
}

.contact-info-header {
    font-size: 30px;
    margin-bottom: 10px;
}

.contact-input {
    margin-bottom: 3px;
    font-size: 18px;
}